import React, { Component } from "react"
import { Button, Modal, ModalBody, Form, ModalFooter } from "react-bootstrap"
import { CheckboxField } from "@components/shared/Fields/CheckboxField"
import KPEditor from "@components/shared/KPEditor"
import { connect } from "react-redux"
import { compose } from "redux"
import { withRouter } from "react-router-dom"
import {
  getGeneratedQuote,
  resetGeneratedQuote,
  getContentEditor,
  addContentEditor,
  resetContentEditor,
  getQuotePrices,
} from "@actions/quote"
import { selectAuthorizationId } from "@selectors/authorization"
import { getProjects } from "@actions/project"
import { selectProject } from "@selectors/project"
import { selectGeneratedQuotes, selectContentEditor, selectQuotePrices } from "@selectors/quote"
import moment from "moment"

class Editor extends Component {
  state = {
    pdfConvert: false,
    data: "",
    numberQuote: Math.floor(Math.random() * 1000000),
  }

  onChange = e => {
    if (e.target.type === "checkbox") {
      if (e.target.checked) {
        this.setState({ pdfConvert: true })
      } else {
        this.setState({ pdfConvert: false })
      }
    }
  }

  componentDidMount() {
    const { templateName, getProjects } = this.props
    this.loadTemplate(templateName)
    getProjects()
  }

  componentDidUpdate(prevProps) {
    const { templateName } = this.props
    if (templateName !== prevProps.templateName) {
      this.loadTemplate(templateName)
    }
  }

  loadTemplate(templateName) {
    const {
      getHtml,
      createdQuote,
      quotesLength,
      alphabet,
      addContentEditor,
      projectId,
      prices,
      project: {
        addressBook: { addressName, postCode, city, country },
      },
    } = this.props
    const head = `<div class="head">
                    <p>${moment(new Date()).format("D MMMM YYYY")}</p>
                    <p>Ref: ${projectId}.${moment(new Date()).format("YYMMDD")}.Q${quotesLength + 1}</p>
                    <p>John Doe</p>
                    <p>KP Acoustics Ltd.</p>
                    <p>By email to:</p>
                    <p>Dear</p>
                    <h2>${projectId}: <a class="address">${addressName}, ${postCode}, ${city}, ${country}</a></h2>
                  </div>`

    const quoteBlock = `<div class="quote-block">${createdQuote
      .map((item, indexSection) => {
        return `<h2>${alphabet[indexSection].toUpperCase()}. ${item.name.toUpperCase()}</h2>
        ${item.services
          .map(
            service =>
              `<h2>${alphabet[service.indexSection].toUpperCase()}.${service.indexService + 1}: ${service.name}</h2>${
                service.content
              }`
          )
          .join(" ")}`
      })
      .join(" ")}</div>`

    const quotePrice = `<div class="quote-price">${createdQuote
      .map((item, indexSection) => {
        return `<h3>${alphabet[indexSection].toUpperCase()}. ${item.name}</h3>
          <table>${item.services
            .map((service, index) => {
              return `<tr><td>${alphabet[service.indexSection].toUpperCase()}.${service.indexService + 1}: ${
                service.name
              } </td><td style="text-align: right"><strong> Â£${prices[`a${index}`]} (+VAT)</strong> </td></tr>`
            })
            .join(" ")}</table>`
      })
      .join(" ")}</div>`

    getHtml(templateName).then(response => {
      this.setState({
        data: response
          .replace('<div class="quote-block"></div>', quoteBlock)
          .replace('<div class="head"></div>', head)
          .replace('<div class="quote-price"></div>', quotePrice),
      })
      addContentEditor(
        response
          .replace('<div class="quote-block"></div>', quoteBlock)
          .replace('<div class="head"></div>', head)
          .replace('<div class="quote-price"></div>', quotePrice)
      )
    })
  }

  onSave = async e => {
    e.preventDefault()
    const {
      saveHtml,
      quoteName,
      htmltopdf,
      hideModal,
      addQuote,
      projectId,
      quotesLength,
      contentEditor,
      resetContentEditor,
      history,
      addHistory,
      consultantId,
      project: {
        addressBook: { addressName, postCode, city, country },
      },
    } = this.props

    const { pdfConvert } = this.state
    await addQuote(projectId.replace(/^0+/, ""), { name: quoteName, converted: pdfConvert })
    !quotesLength && (await addHistory(projectId.replace(/^0+/, ""), { statusId: 2, consultantId }))
    await saveHtml(quoteName, contentEditor)
    pdfConvert &&
      (await htmltopdf(quoteName, {
        projectId,
        quotesLength,
        address: `${addressName}, ${postCode}, ${city}, ${country}`,
      }))
    await hideModal(false)
    await resetContentEditor()
    this.setState({ pdfConvert: false })
    history.push(`/dashboard/project/${projectId}`)
  }

  render() {
    const { data, pdfConvert } = this.state
    const { isModalOpen, hideModal, addContentEditor } = this.props
    return (
      <Modal show={isModalOpen && data} onHide={hideModal} className="editor" centered size="xl">
        <Form noValidate onSubmit={this.onSave}>
          <Modal.Header className="bg-primary" closeButton>
            <h5 className="modal-title has-icon text-white">Editor</h5>
          </Modal.Header>
          <ModalBody>
            <KPEditor data={data} addContentEditor={addContentEditor} />
          </ModalBody>
          <ModalFooter>
            <CheckboxField name="pdfConvert" value={pdfConvert} label="Convert it to PDF?" onChange={this.onChange} />
            <Button variant="primary" className="swal2-styled btn-light" onClick={() => hideModal(false)}>
              Cancel
            </Button>
            <Button variant="primary" type="submit" className="swal2-confirm swal2-styled">
              Save
            </Button>
          </ModalFooter>
        </Form>
      </Modal>
    )
  }
}

const mapStateToProps = (state, props) => {
  return {
    createdQuote: selectGeneratedQuotes(state),
    contentEditor: selectContentEditor(state),
    project: selectProject(state, props.projectId),
    prices: selectQuotePrices(state),
    consultantId: selectAuthorizationId(state),
  }
}

export default compose(
  withRouter,
  connect(mapStateToProps, {
    getGeneratedQuote,
    resetGeneratedQuote,
    getContentEditor,
    addContentEditor,
    resetContentEditor,
    getProjects,
    getQuotePrices,
  })
)(Editor)
